// ------------- AUTOCOMPLETE INPUT - OVERRIDES ------------- //
.search__t {
    overflow: visible!important;
    margin-left: -12px!important;
}

.search__go {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.search__c3:before {
