// ------------- AUTOCOMPLETE INPUT - DROPDOWN ------------- //
.ac-input-dropdown {
    opacity: 0;
    visibility: hidden;
    &.open {
        opacity: 1;
        visibility: visible;
        transition: .15s;
    }
}